type ElementItem = {
  type: string;
  offsetX: number;
  offsetY: number;
  lastOffsetX: number;
  lastOffsetY: number;
  width: number;
  height: number;
};

interface Elements {
  [key: string]: ElementItem;
}

interface ElementsAction {
  type: string;
  payload: any;
}

interface ReducerState {
  count: number;
  elements: Elements;
}

function reducer(state: ReducerState, action: ElementsAction) {
  switch (action.type) {
    case "add":
      return {
        count: state.count + 1,
        elements: Object.assign(state.elements, action.payload),
      };

    case "delete":
      const nextStateElements = { ...state.elements };
      delete nextStateElements[action.payload.key];
      return {
        count: state.count - 1,
        elements: nextStateElements,
      };

    case "update":
      return {
        count: state.count,
        elements: { ...state.elements, ...action.payload },
      };

    default:
      throw new Error();
  }
}

export { reducer };
export type { Elements, ElementItem, ReducerState };
