import type { DraggableData, DraggableEvent } from "react-draggable";
import type { ReducerState, ElementItem } from "../../reducers";

import { useEffect, useRef, useState } from "react";
import cn from "classnames";
import { throttle } from "lodash";
import Draggable from "react-draggable";
import { Resizable } from "react-resizable";
import { drawRuler } from "../../utils/drawRuler";

import styles from "./index.module.scss";

interface IProps {
  state: ReducerState;
  dispatch: any;
}

interface spacingOffset {
  lineStyle: React.CSSProperties;
  spanStyle: React.CSSProperties;
}

function RulerCanvas(props: IProps) {
  const { state, dispatch } = props;
  const [box, setBox] = useState({ width: 200, height: 200 });
  const abscissaRef = useRef<HTMLCanvasElement>(null);
  const ordinateRef = useRef<HTMLCanvasElement>(null);
  const [draggingElement, setDraggingElement] = useState<ElementItem>();
  const [spacingOffset, setSpacingOffset] = useState<spacingOffset[]>([]);

  useEffect(() => {
    if (abscissaRef.current) {
      drawRuler(abscissaRef.current, "horizontal");
    }
  }, []);

  useEffect(() => {
    if (ordinateRef.current) {
      drawRuler(ordinateRef.current, "vertical");
    }
  }, []);

  const onResize = (_event: any, { size }: any) => {
    setBox({ width: size.width, height: size.height });
  };

  const onStart = (key: string) => {
    setDraggingElement(state.elements[key]);
  };

  const onStop = (key: string) => {
    setSpacingOffset([]);
    setDraggingElement(undefined);
    dispatch({ type: "update", payload: { [key]: draggingElement } });
  };

  const calculateGuides = (key: string) => {
    if (draggingElement) {
      const currElements = {
        ...state.elements,
        [key]: draggingElement,
      };
      const startY = currElements[key].lastOffsetY;
      const startX = currElements[key].lastOffsetX;

      const intersectionStartY = Object.keys(currElements)
        .filter(
          (i) =>
            currElements[i].lastOffsetY === startY ||
            currElements[i].lastOffsetY + currElements[i].height === startY
        )
        .sort(
          (a, b) => currElements[a].lastOffsetX - currElements[b].lastOffsetX
        );
      const intersectionStartX = Object.keys(currElements)
        .filter(
          (i) =>
            currElements[i].lastOffsetX === startX ||
            currElements[i].lastOffsetX + currElements[i].width === startX
        )
        .sort(
          (a, b) => currElements[a].lastOffsetY - currElements[b].lastOffsetY
        );
      const nextSpacingsY: spacingOffset[] = [];
      const nextSpacingsX: spacingOffset[] = [];

      if (intersectionStartY.length > 1) {
        intersectionStartY.forEach((pos, index, arr) => {
          if (arr[index + 1]) {
            const leftWith =
              currElements[pos].lastOffsetX + currElements[pos].width;
            nextSpacingsY.push({
              lineStyle: {
                left: leftWith,
                width: currElements[arr[index + 1]].lastOffsetX - leftWith,
                top: startY,
                height: 1,
                flexDirection: "row",
              },
              spanStyle: {
                top: 0,
              },
            });
          }
        });
      }

      if (intersectionStartX.length > 1) {
        intersectionStartX.forEach((pos, index, arr) => {
          if (arr[index + 1]) {
            const topHeight =
              currElements[pos].lastOffsetY + currElements[pos].height;
            nextSpacingsX.push({
              lineStyle: {
                top: topHeight,
                height: currElements[arr[index + 1]].lastOffsetY - topHeight,
                left: startX,
                width: 1,
                flexDirection: "column",
              },
              spanStyle: {
                left: 3,
              },
            });
          }
        });
      }
      setSpacingOffset([...nextSpacingsY, ...nextSpacingsX]);
    }
  };

  const handleDrag = (_e: DraggableEvent, ui: DraggableData, key: string) => {
    if (draggingElement) {
      setDraggingElement({
        ...state.elements[key],
        lastOffsetX: draggingElement.lastOffsetX + ui.deltaX,
        lastOffsetY: draggingElement.lastOffsetY + ui.deltaY,
      });
      calculateGuides(key);
    }
  };

  return (
    <div className={styles.ruler}>
      <div className={styles.aided}>
        <svg
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="3316"
          width="15"
          height="15"
        >
          <path
            d="M821.776 194.237c11.558 11.425 11.844 29.94 0.782 41.714l-0.544 0.564-611.895 618.61c-11.613 11.74-30.547 11.846-42.29 0.237-11.558-11.425-11.844-29.939-0.782-41.714l0.544-0.564 611.895-618.61c11.612-11.74 30.547-11.846 42.29-0.237z m-47.783 128.519c47.302 47.427 91.791 106.644 133.437 177.482l3.57 6.114-1.124 0.65 1.124 0.652C792.227 712.385 650.134 821.879 485.434 832.2v0.805l-5.264-0.033c-2.562-0.032-5.12-0.088-7.671-0.167-2.549 0.08-5.106 0.135-7.669 0.167l-5.264 0.033v-0.805c-53.85-3.375-105.284-17.351-154.276-41.792l45.141-45.035c39.246 16.884 79.93 26.052 122.07 27.604 138.576-5.104 261.409-92.568 369.145-265.97v-0.01c-35.042-56.4-71.682-103.709-109.94-142.049l42.287-42.192zM485.434 181v0.805c70.587 4.424 137.021 27.063 199.247 67.609l-43.445 43.347C587.82 260.357 531.592 243.205 472.5 241.03c-138.578 5.103-261.41 92.567-369.147 265.969v0.01c46.173 74.315 95.12 132.846 146.889 175.874l-42.468 42.375C146.6 673.293 89.845 602.686 37.57 513.767L34 507.654l1.123-0.652-1.123-0.65C152.773 301.62 294.866 192.127 459.566 181.805V181l5.264 0.033c2.563 0.032 5.12 0.088 7.672 0.167a414.36 414.36 0 0 1 7.668-0.167l5.264-0.033zM621.7 474.712c4.236 13.255 6.523 27.38 6.523 42.039 0 76.204-61.794 137.98-138.021 137.98-14.759 0-28.977-2.316-42.31-6.604l54.21-54.087c33.617-5.13 60.192-31.655 65.389-65.24l54.21-54.088z m-131.498-95.941c19.122 0 37.336 3.888 53.894 10.915l-49.13 49.019a79.448 79.448 0 0 0-4.764-0.143c-43.196 0-78.212 35.006-78.212 78.189 0 1.548 0.045 3.085 0.133 4.61l-49.129 49.021c-6.963-16.487-10.814-34.61-10.814-53.631 0-76.204 61.795-137.98 138.022-137.98z"
            fill="#515151"
          ></path>
        </svg>
      </div>
      <canvas ref={abscissaRef} className={styles.abscissa} />
      <canvas ref={ordinateRef} className={styles.ordinate} />
      <div className={styles.container}>
        <div className={styles.backdrop}>
          <div className={styles.guides}>
            {draggingElement ? (
              <div
                className={styles.line}
                style={{
                  width: draggingElement.lastOffsetX,
                  height: draggingElement.lastOffsetY,
                }}
              >
                <span>{`${draggingElement.lastOffsetX * 2}, ${
                  draggingElement.lastOffsetY * 2
                }`}</span>
              </div>
            ) : null}
          </div>
          {spacingOffset.length > 0 &&
            spacingOffset.map(
              (item, index) =>
                Number(item?.lineStyle?.height) > 0 &&
                Number(item?.lineStyle?.width) > 0 && (
                  <div
                    key={`spaceing-offset-${index}`}
                    className={cn(
                      styles.spacing,
                      {
                        [styles.vertical]: Number(item?.lineStyle?.height) > 1,
                      },
                      {
                        [styles.horizon]: Number(item?.lineStyle?.height) <= 1,
                      }
                    )}
                    style={item.lineStyle}
                  >
                    <span style={item.spanStyle}>
                      {Number(item?.lineStyle?.height) > 1
                        ? item.lineStyle.height
                        : item.lineStyle.width}
                    </span>
                  </div>
                )
            )}
          {Object.keys(state.elements).map((item) => (
            <Draggable
              key={item}
              bounds="parent"
              scale={1}
              onStart={() => onStart(item)}
              onStop={() => onStop(item)}
              onDrag={(e, ui) => handleDrag(e, ui, item)}
              handle={`.${styles.item}`}
            >
              <div
                className={styles.item}
                style={{
                  top: state.elements[item].offsetY,
                  left: state.elements[item].offsetX,
                  width: state.elements[item].width,
                  height: state.elements[item].height,
                  zIndex: 9 + item,
                }}
              >
                第{item}个元素
              </div>
            </Draggable>
          ))}
          {/* <Resizable
            className={styles.box}
            height={box.height}
            width={box.width}
            onResize={onResize}
          >
            <div
              style={{ width: `${box.width}px`, height: `${box.height}px` }}
            ></div>
          </Resizable> */}
        </div>
      </div>
    </div>
  );
}

export default RulerCanvas;
