import type { Elements, ElementItem } from "../reducers";

const generateReactCode = (resouce: Elements) => {
  let code = `import React from "react";

const BILargeScreen = () => {

  const wrapperStyle: React.CSSProperties = {
    width: 960,
    height: 540,
    backgroundColor: 'rgb(35, 38, 48)',
    position: 'relative'
  };

  const blockStyle: React.CSSProperties = {
    color: '#FFF',
    fontSize: 10,
    border: '1px solid #FFF',
    position: 'absolute'
  };

  return (
    <div style={wrapperStyle}>
      ${Object.keys(resouce)
        .map(
          (item: any) =>
            `<div style={{ ...blockStyle, ${generateReactStyle(
              resouce[item],
              item
            )} }}>第${item}个元素</div>`
        )
        .join("\n      ")}
    </div>
  )
}

export default BILargeScreen;
`;
  return code;
};

const generateReactStyle = (element: ElementItem, key: string) => {
  const { offsetY, offsetX, width, height } = element;
  return `top: ${offsetY}, left: ${offsetX}, width: ${width}, height: ${height}, zIndex: ${
    9 + key
  }`;
};

export { generateReactCode };
