import type { Elements } from "./reducers";

import { useEffect, useReducer, useRef, useState } from "react";
import { PrismLight as SyntaxHighlighter } from "react-syntax-highlighter";
import jsx from "react-syntax-highlighter/dist/esm/languages/prism/jsx";
import prism from "react-syntax-highlighter/dist/esm/styles/prism/prism";
import cn from "classnames";

import { Drawer, RulerCanvas } from "./components";
import { reducer } from "./reducers";
import { generateReactCode } from "./utils/utils";

import styles from "./assets/scss/app.module.scss";

SyntaxHighlighter.registerLanguage("jsx", jsx);

function App() {
  const initialState = { count: 0, elements: {} };
  const [state, dispatch] = useReducer(reducer, initialState);
  const [collapsed, setCollapsed] = useState(false);
  const [isDragging, setDragging] = useState(false);
  const [visible, setVisible] = useState<boolean>(false);

  // A flag to check if a file is being dragged in from the operating system.
  const isFileFromOS = useRef(true);
  // We will cache the target that we first drag over when dragenter fires.
  // This will allow us to compare the cached value with the target that
  // dragleave returns and check whether the dragged item is really out of view.
  const cachedTarget = useRef<EventTarget | null>(null);

  const hasDraggedFileFromBrowserOutsideOfWindow = useRef(false);

  useEffect(() => {
    const handleDragEnter = (e: DragEvent) => {
      cachedTarget.current = e.target;
    };
    // dragstart event only fires when a file is dragged from within the browser
    // window. It won't be fired when coming from the OS.
    const handleDragStart = () => {
      isFileFromOS.current = false;
      hasDraggedFileFromBrowserOutsideOfWindow.current = false;
      console.log("Drag started from browser");
    };

    const handleDragOver = (e: DragEvent) => {
      // e.preventDefault will allow us to drag a file in the browser without it opening.
      e.preventDefault();

      // If document still has focus, that means the user never dropped the file.
      if (
        hasDraggedFileFromBrowserOutsideOfWindow.current &&
        document.hasFocus()
      ) {
        console.log("Dragged file back into view from browser");
      } else if (isFileFromOS.current) {
        console.log("Dragging from OS");
        hasDraggedFileFromBrowserOutsideOfWindow.current = false;

        if (!isDragging) setDragging(true);
      } else {
        console.log("Dragging from browser");
      }
    };

    const handleDragLeave = (e: DragEvent) => {
      if (e.target === cachedTarget.current) {
        if (
          isFileFromOS.current &&
          !hasDraggedFileFromBrowserOutsideOfWindow.current
        ) {
          console.log("Left view from OS");

          if (isDragging) setDragging(false);
        } else {
          // Left view from browser. If a user drops an item here, we won't be able to
          // capture that event since it's outside of the window. We need to somehow
          // find out if at any point the document loses focus; aka the user opened
          // a Finder/Explorer window to drag a new file in.
          // We will set a flag to signal that the file has been dragged outside of
          // the window so our dragover handler can compare this with document.hasFocus()
          // to check if the user has tabbed out to drag another file from the OS.
          console.log("Left view from browser");
          hasDraggedFileFromBrowserOutsideOfWindow.current = true;
          // Reset isFileFromOS flag
          isFileFromOS.current = true;
        }
      }
    };

    const handleDrop = (e: DragEvent) => {
      // Again, e.preventDefault needs to be here and on dragover event
      // to prevent the file from opening in the browser.
      console.log("drop", e);
      e.preventDefault();
      // Reset isFileFromOS flag
      isFileFromOS.current = true;
      hasDraggedFileFromBrowserOutsideOfWindow.current = false;
      let payload: Elements = {};
      payload[state.count.toString()] = {
        type: "div",
        offsetX: e.offsetX - 50,
        offsetY: e.offsetY - 50,
        lastOffsetX: e.offsetX - 50,
        lastOffsetY: e.offsetY - 50,
        width: 100,
        height: 100,
      };
      dispatch({ type: "add", payload });
      if (isDragging) setDragging(false);
    };

    window.addEventListener("dragenter", handleDragEnter);
    window.addEventListener("dragstart", handleDragStart);
    window.addEventListener("dragover", handleDragOver);
    window.addEventListener("dragleave", handleDragLeave);
    window.addEventListener("drop", handleDrop);

    return () => {
      window.removeEventListener("dragenter", handleDragEnter);
      window.removeEventListener("dragstart", handleDragStart);
      window.removeEventListener("dragover", handleDragOver);
      window.removeEventListener("dragleave", handleDragLeave);
      window.removeEventListener("drop", handleDrop);
    };
  }, [isDragging, state.count]);

  return (
    <div className={styles.container}>
      <div className={cn(styles.sider, { [styles.unflod]: collapsed })}>
        <div className={styles.flod} onClick={() => setCollapsed(false)}>
          <svg
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="2047"
            width="22"
            height="22"
          >
            <path
              d="M0 0h1024v1024H0z"
              fill="#FFFFFF"
              fillOpacity=".01"
              p-id="2048"
            ></path>
            <path
              d="M853.333333 192V256H170.666667V192zM725.333333 490.666667V554.666667H170.666667v-64zM853.333333 789.333333V853.333333H170.666667v-64z"
              fill="#666666"
              p-id="2049"
            ></path>
          </svg>
        </div>
        <div className={styles.menus}>
          <ul>
            <li draggable>
              <svg
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                width="23"
                height="23"
                fill="#e8e8e8"
              >
                <path
                  d="M896 480c-1.344 0-2.464 0.608-3.744 0.768-1.28-0.16-2.432-0.768-3.744-0.768H544V132c0-0.704-0.352-1.312-0.416-2.016 0.064-0.672 0.416-1.28 0.416-1.984a32 32 0 0 0-32-32C282.624 96 96 282.624 96 512s186.624 416 416 416 416-186.624 416-416a32 32 0 0 0-32-32zM512 864C317.92 864 160 706.08 160 512 160 328.704 300.864 177.856 480 161.632V512a32 32 0 0 0 32 32h350.368C846.144 723.136 695.296 864 512 864zM625.664 178.72a355.36 355.36 0 0 1 216.832 211.84 32 32 0 1 0 60.064-22.048 414.24 414.24 0 0 0-256.224-250.336 31.968 31.968 0 1 0-20.672 60.544z"
                  p-id="9186"
                ></path>
              </svg>
            </li>
            <li draggable>
              <svg
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                width="18"
                height="18"
                fill="#e8e8e8"
              >
                <path
                  d="M960.031235 159.921913v703.656418c0 52.974134-42.979014 95.953148-95.953148 95.953148h-703.656418c-52.974134 0-95.953148-42.979014-95.953148-95.953148v-703.656418c0-52.974134 42.979014-95.953148 95.953148-95.953148h703.656418c52.974134 0 95.953148 42.979014 95.953148 95.953148z m-831.593949-159.921913C57.771791 0 0.499756 57.272035 0.499756 127.937531v767.625183c0 70.665495 57.272035 127.937531 127.93753 127.93753h767.625183c70.665495 0 127.937531-57.272035 127.937531-127.93753v-767.625183c0-70.665495-57.272035-127.937531-127.937531-127.937531h-767.625183z"
                  p-id="3754"
                ></path>
              </svg>
            </li>
          </ul>
        </div>
      </div>
      <div className={styles.content}>
        <div className={styles.tool}>
          <span onClick={() => setVisible(!visible)}>
            <svg
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              width="26"
              height="26"
              fill="#e8e8e8"
            >
              <path d="M438.4 849.1l222.7-646.7c0.2-0.5 0.3-1.1 0.4-1.6L438.4 849.1z"></path>
              <path d="M661.2 168.7h-67.5c-3.4 0-6.5 2.2-7.6 5.4L354.7 846c-0.3 0.8-0.4 1.7-0.4 2.6 0 4.4 3.6 8 8 8h67.8c3.4 0 6.5-2.2 7.6-5.4l0.7-2.1 223.1-648.3 7.4-21.4c0.3-0.8 0.4-1.7 0.4-2.6-0.1-4.5-3.6-8.1-8.1-8.1zM954.6 502.1c-0.8-1-1.7-1.9-2.7-2.7l-219-171.3c-3.5-2.7-8.5-2.1-11.2 1.4-1.1 1.4-1.7 3.1-1.7 4.9v81.3c0 2.5 1.1 4.8 3.1 6.3l115 90-115 90c-1.9 1.5-3.1 3.8-3.1 6.3v81.3c0 4.4 3.6 8 8 8 1.8 0 3.5-0.6 4.9-1.7l219-171.3c6.9-5.4 8.2-15.5 2.7-22.5zM291.1 328.1l-219 171.3c-1 0.8-1.9 1.7-2.7 2.7-5.4 7-4.2 17 2.7 22.5l219 171.3c1.4 1.1 3.1 1.7 4.9 1.7 4.4 0 8-3.6 8-8v-81.3c0-2.5-1.1-4.8-3.1-6.3l-115-90 115-90c1.9-1.5 3.1-3.8 3.1-6.3v-81.3c0-1.8-0.6-3.5-1.7-4.9-2.7-3.5-7.7-4.1-11.2-1.4z"></path>
            </svg>
          </span>
        </div>
        <div className={styles.edit}>
          <RulerCanvas state={state} dispatch={dispatch} />
        </div>
      </div>
      <Drawer visible={visible} onClose={() => setVisible(false)}>
        <SyntaxHighlighter language="jsx" style={prism} showLineNumbers>
          {generateReactCode(state.elements)}
        </SyntaxHighlighter>
      </Drawer>
    </div>
  );
}

export default App;
